* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@-webkit-keyframes loader {
  from {
    left: 80px;
  }
  to {
    left: -80px;
  }
}

@keyframes loader {
  from {
    left: 80px;
  }
  to {
    left: -80px;
  }
}

.loader {
  position: relative;
  display: block;
  width: 80px;
  height: 30px;
  margin: 0 auto;
  background: #ccc;
  background: -webkit-linear-gradient(left, #ffffff 0%,#cccccc 50%,#ffffff 100%);
  background: linear-gradient(to right, #ffffff 0%,#cccccc 50%,#ffffff 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#ffffff',GradientType=1 );
  -webkit-animation: linear loader 1s infinite alternate;
          animation: linear loader 1s infinite alternate;
}

.intro .img {
  position: relative;
  display: inline-block;
}

.error .code {
  position: relative;
  font-size: 36px;
  font-weight: 900;
  color: #000;
  top: -14px;
}

.error p {
  display: inline-block;
  font-size: 26px;
  color: #ccc;
}

.error {
  text-align: center;
}

@-webkit-keyframes blink {
  from {
    background: #4CAF50;
  }

  to {
    background: #fff;
  }
}

@keyframes blink {
  from {
    background: #4CAF50;
  }

  to {
    background: #fff;
  }
}

.intro .img:after {
  content: "";
  position: absolute;
  bottom: 15px;
  right: 15px;
  display: block;
  height: 30px;
  width: 30px;
  background: #4CAF50;
  border-radius: 100%;
  border: solid 5px #fff;
  -webkit-animation: cubic-bezier(0.4, 0, 1, 1) blink 1s alternate infinite;
          animation: cubic-bezier(0.4, 0, 1, 1) blink 1s alternate infinite;
}

.intro .img img {
    border-radius: 100%;
    padding: 10px;
    height: 160px;
    width: 160px;
    border: dotted 4px #000;
}

.container {
    width: 767px;
    margin: 100px auto;
}

.intro {
  text-align: center;
  padding-bottom: 100px;
  border-bottom: solid 4px #000;
}

.intro h1 {
    text-transform: uppercase;
    margin-top: 5px;
    font-weight: 900;
}

.intro .username {
    font-weight: 900;
    font-size: 20px;
    color: #727272;
    letter-spacing: 1px;
    margin-bottom: 15px;
}

.intro .username:before {
    content: "(";
}

.intro .username:after {
    content: ")";
}

.intro .bio {
  margin-bottom: 15px;
}

.intro .list {
  text-transform: uppercase;
  font-weight: 900;
}

.repos {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.repos>* {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  -webkit-flex-shrink: 1;
          flex-shrink: 1;
  -webkit-flex-basis: calc(50% - 60px);
          flex-basis: calc(50% - 60px);
}

.repo {
  position: relative;
  background: #000;
  color: #fff;
  padding: 25px;
  margin: 5px;
}

.repo h3 a {
  color: #fff;
  text-decoration: none;
}

.repo ul li {
  display: inline-block;
  font-family: monospace;
  font-size: 18px;
  list-style: none;
  margin-right: 15px;
}

.repo ul {
  margin-top: 15px;
}

.repo h3 {
  margin-bottom: 15px;
}

.footer {
  padding-top: 25px;
  border-top: solid 4px #000;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.footer>* {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.footer ul li {
  list-style: none;
  display: inline-block;
  margin-right: 15px;
}

.footer p {
  text-align: right;
}

.footer p strong {
  font-weight: 900;
}

